@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;600&display=swap);
:root{
	--orange_120: #E87928;
	--orange_100:#FF8B37;
	--orange_80:#FFAC70;
	--orange_5:#FFF3EB;
	--green_100:#47D867;
	--green_5:#E6FAEA;
	--red_100:#EC6259;
	--red_5:#FDEEED;
	--black_100:#1C1C21;
	--black_40:#D0D0D8;
	--black_5:#F5F5F7;
	--white_100: #ffffff;
}

*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

button:active {
	border-style: none;
}


html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body, html{
  font-family: 'Inter', sans-serif;
}

.main{
  font-size: 56px;
  font-weight: 600;
}

h1{
	font-weight: 600;
	font-size: 56px;
	line-height: 116%;
	color: #1C1C21;
	color: var(--black_100);
}

h2{
	font-weight: 600;
	font-size: 32px;
	line-height: 116%;
	color: #1C1C21;
	color: var(--black_100);
}

h2.gray{
	color: #D0D0D8;
	color: var(--black_40);
}

p{
	font-size: 20px;
	line-height: 116%;
	color: #1C1C21;
	color: var(--black_100);
}

p.orange{
	color: #FF8B37;
	color: var(--orange_100);
}

.container-fluid{
	width: 100%;
	min-height: 100vh;
	display: flex;
	justify-content: center;
	background: #ffffff;
	background: var(--white_100);
	position: relative;
}

.orange-triangle{
	position: absolute;
	width: 100%;
	height: 100%;
	z-index: 0;
	top: 0;
	left: 0;
	-webkit-clip-path: polygon(100% 0, 0% 100%, 100% 100%);
	        clip-path: polygon(100% 0, 0% 100%, 100% 100%);
  background: #FFF3EB;
  background: var(--orange_5)
}


.container-fluid.gray{
	background: #E5E5E5;
}

.container{
	width: 100%;
	padding: 0 30px;
	max-width: 1340px;
	min-height: 100%;
	display: flex;
	align-items: center;
	position: relative;
	z-index: 1;
}

.main-page, .final-page{
  width: 100%;
  display: flex;
  align-items: center;
  margin: auto 0;
}

.action-button{
	width: 296px;
	height: 64px;
	border-radius: 12px;
	border: 0;
	font-weight: 600;
	font-size: 20px;
	text-align: center;
	color: #ffffff;
	color: var(--white_100);
	background: #FF8B37;
	background: var(--orange_100);
	transition: 0.3s;
	display: flex;
	align-items: center;
	justify-content: center;
	text-decoration: none;
}

.action-button.margit-top{
	margin-top: 64px;
}

@media screen and (min-width: 768px){
	.action-button:hover{
		background: #FFAC70;
		background: var(--orange_80)
	}
}

.action-button:active{
	background: #E87928;
	background: var(--orange_120)
}


.answer-option-button{
	width: 50%;
	padding: 0 48px;
	height: 72px;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	margin-top: 32px;
}

.answer-option-button__bg{
	position: absolute;
	width: 100%;
	height: 72px;
	top: 0;
	left: 0;
}

.answer-option-button__bg svg{
	width: 100%;
}

.answer-option-button__bg path{
	stroke: var(--black_40);
	fill: var(--white_100);
	transition: 0.3s;
}

.answer-option-button.correct .answer-option-button__bg path{
	stroke: var(--green_100);
	fill: var(--green_5);
}

.answer-option-button.wrong .answer-option-button__bg path{
	stroke: var(--red_100);
	fill: var(--red_5);
}

.answer-option-button__body{
	display: flex;
	align-items: flex-start;
	position: relative;
	z-index: 1;
}

.answer-option-button__index{
	margin-right: 8px;
}

.answer-option-button input{
	display: none;
}

.answer-option-button input:checked + .answer-option-button__bg path{
	stroke: var(--orange_100);
	fill: var(--orange_5);
}
.answer-value-button{
  width: 100%;
  height: 40px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 8px;
}

.answer-value-button__bg{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 2;
}

.answer-value-button__bg::before{
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background: var(--black_40);
  position: absolute;
  top: calc(50% - 2px);
  z-index: -1;
  transform: translate(0, 50%);
}

.answer-value-button__bg svg{
  width: 100%;
}

.answer-value-button__bg path{
	stroke: var(--black_40);
	fill: var(--white_100);
}

.answer-value-button__value{
  position: relative;
  z-index: 3;
}

.answer-value-button.active .answer-value-button__value{
  color: var(--orange_100)
}

.answer-value-button.active .answer-value-button__bg path{
	stroke: var(--orange_100);
}

.answer-value-button.done .answer-value-button__value{
  color: var(--black_40)
}

.answer-value-button.done .answer-value-button__bg path{
	stroke: var(--black_40);
}




.questions-page{
  width: 100%;
  display: flex;
}

.questions-page__body{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  max-width: 1014px;
  padding: 0 30px;
  margin-left: auto;
}

.questions-page__score{
  width: 20%;
  min-width: 376px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: var(--white_100)
}

.answer-option{
	display: flex;
  align-items: center;
  flex-wrap: wrap;
	max-width: 844px;
	width: 100%;
}
